@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400..700&family=Raleway:ital,wght@0,100..900;1,100..900&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

html {
  scroll-behavior: smooth;
}

html, body {
  height: 100%;
  background-color: black; 
  color: white;
  margin: 0; 
}

h1, h2 { 
  @apply text-4xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-orange-400 to-pink-600 inline-block; 
  font-family: 'Dancing Script', cursive;
}

.my-custom-class {
  @apply text-pink-800; 
}

.bg-primary-color {
  @apply px-1 py-2 w-full rounded-xl mr-4 bg-gradient-to-r from-orange-500 to-pink-500 text-black font-bold hover:border-none; 
}


.navbar{
  background-color: black;
  padding: 1rem;
  z-index: 10;
  width: 100%;
}

.navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 50; 
}

.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 50; 
}

/* Section Styling */
section {
  min-height: 100vh; /* Ensures sections fill at least the viewport height */
  padding-top: 4rem;  /* Makes room for fixed navbar */
  padding-bottom: 4rem; /* Makes room for fixed footer */
}

section.hero {
  padding-top: 6rem; /* More padding for the hero section */
}

section.skills, section.contact {
  padding: 2rem;
}

/* Flex Container for Layout */
.flex-container {
  @apply flex flex-col min-h-screen; /* Ensures full height layout */
}

/* Responsiveness */
@media (max-width: 640px) {
  .navbar, .footer {
    padding: 0.5rem; /* Adjust padding on smaller screens */
  }

  section {
    padding-top: 2rem;
    padding-bottom: 2rem;
  }
}
